
import '../css/style.css';
import {HashRouter, Route, Routes, Link,useLocation} from "react-router-dom";
import { useEffect, useState } from 'react';

function Main({k}) {
    const imgs = new Array("./img/visual_01.jpg","./img/visual_02.jpg","./img/visual_03.jpg","./img/visual_04.jpg","./img/visual_05.jpg");
    const [imgUrl,setImgUrl] = useState(imgs[0]);


    useEffect(()=>{      
        setImgUrl(imgs[k]);
    },[k])
    
    // 출처 : http://madalla.kr/bbs/board.php?bo_table=data_script&wr_id=32&sst=wr_hit&sod=desc&sop=and&page=2
  

  return (
    <>
    <main>
        <div className="content">
            <div className="visual cnt01">
                <img src={imgUrl} id="slide" />
                <div className="per"><img src="img/visual_person.png" alt="" /></div>
                <div className="tit">
                    <h2><div>Publisher</div> <div><span>&amp; Front-end</span></div></h2>
                    <h3><div><img src="img/powerwmj.svg" alt="powerwmj" /></div></h3>
                    <p>오늘 할 수 있는 일에 전력을 다하라.<br />그러면 내일에는 <span>한걸음 더 진보</span>한다. - 뉴턴 -</p>
                </div>
            </div>
        </div>
    </main>
    </>
  );
}

export default Main;